.products-categories {
  font-size: 19px;
  line-height: 24px;
  font-weight: 700;
}

.categories-list{
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.categories-list__item {
  width: 320px;
  min-height: 103px;
  text-align: left;
  padding: 0 30px;
  display: flex;
  align-items: center;
}

.categories-list__link  {
  color: #ffffff;
  min-width: 130px;
  padding: 24px 160px 31px 0;
}

.categories-list__item--primary  {
  background-color: #63D1BB;
}

.categories-list__item--secondary  {
  background-color: #56C3B2;
}
